/* src/App.css */
:root {
  /* Binance Color Palette */
  --primary-dark: #0c0e16;
  --secondary-dark: #161a25;
  --tertiary-dark: #1e2329;
  --accent-gold: #fcd535;
  --accent-green: #02c076;
  --accent-red: #f6465d;
  --accent-blue: #1890ff;
  --accent-purple: #722ed1;
  --text-primary: #eaecef;
  --text-secondary: #848e9c;
  --text-muted: #5e6673;
  --border-color: #2b3139;
  --hover-bg: #2b3139;
  --sidebar-width: 250px;
  --header-height: 64px;
  --border-radius: 8px;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 4px 12px rgba(0, 0, 0, 0.15);
  --shadow-lg: 0 8px 24px rgba(0, 0, 0, 0.2);
  --transition-fast: 0.15s ease;
  --transition-normal: 0.25s ease;
  --transition-slow: 0.35s ease;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
  background: var(--primary-dark);
  color: var(--text-primary);
  line-height: 1.6;
  overflow-x: hidden;
  font-size: 14px;
}

.App {
  display: flex;
  min-height: 100vh;
}

/* Sidebar Styles */
.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  width: var(--sidebar-width);
  height: 100vh;
  background: linear-gradient(180deg, var(--secondary-dark) 0%, var(--tertiary-dark) 100%);
  border-right: 1px solid var(--border-color);
  z-index: 1000;
  transition: transform var(--transition-normal);
  overflow-y: auto;
  box-shadow: var(--shadow-lg);
  display: flex;
  flex-direction: column;
}

.sidebar.collapsed {
  transform: translateX(-100%);
}

.sidebar-header {
  padding: 20px 20px 16px;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo-icon {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, var(--accent-gold) 0%, #f0b90b 100%);
  border-radius: var(--border-radius);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-dark);
  font-weight: bold;
  font-size: 18px;
  box-shadow: 0 4px 12px rgba(252, 213, 53, 0.3);
  flex-shrink: 0;
}

.logo-icon.large {
  width: 60px;
  height: 60px;
  font-size: 24px;
}

.logo-text h1 {
  font-size: 16px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 2px;
  line-height: 1.2;
}

.logo-text p {
  font-size: 11px;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.sidebar-nav {
  padding: 20px 0;
  flex: 1;
}

.nav-section {
  margin-bottom: 24px;
}

.nav-section-title {
  padding: 0 20px 12px;
  font-size: 11px;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.8px;
  font-weight: 600;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 20px;
  cursor: pointer;
  transition: all var(--transition-fast);
  border-left: 3px solid transparent;
  position: relative;
}

.nav-item:hover {
  background: var(--hover-bg);
  border-left-color: var(--accent-gold);
}

.nav-item.active {
  background: linear-gradient(90deg, transparent 0%, rgba(252, 213, 53, 0.1) 100%);
  border-left-color: var(--accent-gold);
  color: var(--accent-gold);
}

.nav-item i {
  width: 20px;
  text-align: center;
  font-size: 16px;
  flex-shrink: 0;
}

.nav-item span {
  font-weight: 500;
  font-size: 14px;
  flex: 1;
}

.alert-badge {
  background: var(--accent-red);
  color: white;
  font-size: 10px;
  font-weight: 600;
  padding: 2px 6px;
  border-radius: 10px;
  min-width: 18px;
  text-align: center;
  margin-left: auto;
}

.sidebar-footer {
  padding: 20px;
  border-top: 1px solid var(--border-color);
  margin-top: auto;
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 12px;
  font-size: 12px;
  color: var(--text-secondary);
}

.status-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--text-muted);
  transition: all var(--transition-fast);
}

.status-dot.online {
  background: var(--accent-green);
  box-shadow: 0 0 8px rgba(2, 192, 118, 0.4);
}

.status-dot.offline {
  background: var(--accent-red);
}

.sidebar-version {
  font-size: 11px;
  color: var(--text-muted);
  text-align: center;
}

/* Main Content */
.main-content {
  margin-left: var(--sidebar-width);
  min-height: 100vh;
  transition: margin-left var(--transition-normal);
  display: flex;
  flex-direction: column;
  flex: 1;
}

.main-content.sidebar-collapsed {
  margin-left: 0;
}

/* Header */
.header {
  background: linear-gradient(135deg, var(--secondary-dark) 0%, var(--tertiary-dark) 100%);
  padding: 0 24px;
  height: var(--header-height);
  border-bottom: 1px solid var(--border-color);
  box-shadow: var(--shadow-sm);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 20px;
}

.sidebar-toggle {
  background: none;
  border: 1px solid var(--border-color);
  color: var(--text-primary);
  padding: 8px 12px;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: all var(--transition-fast);
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sidebar-toggle:hover {
  background: var(--hover-bg);
  border-color: var(--accent-gold);
}

.page-title {
  font-size: 20px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
}

.breadcrumb {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 12px;
  color: var(--text-secondary);
}

.breadcrumb i {
  font-size: 10px;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 24px;
}

.header-stats {
  display: flex;
  gap: 20px;
  font-size: 12px;
}

.stat-item {
  text-align: center;
}

.stat-value {
  color: var(--accent-green);
  font-weight: 600;
  font-size: 14px;
  margin-bottom: 2px;
}

.stat-label {
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 11px;
}

.header-actions {
  display: flex;
  gap: 8px;
}

.header-btn {
  background: var(--tertiary-dark);
  border: 1px solid var(--border-color);
  color: var(--text-secondary);
  width: 36px;
  height: 36px;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.header-btn:hover {
  background: var(--hover-bg);
  color: var(--text-primary);
  border-color: var(--accent-gold);
}

.header-btn.spinning {
  animation: spin 1s linear infinite;
}

.notification-count {
  position: absolute;
  top: -4px;
  right: -4px;
  background: var(--accent-red);
  color: white;
  font-size: 10px;
  font-weight: 600;
  padding: 2px 5px;
  border-radius: 8px;
  min-width: 16px;
  text-align: center;
}

/* Page Content */
.page-content {
  flex: 1;
  padding: 24px;
  overflow-y: auto;
}

.page-section {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Welcome Banner */
.welcome-banner {
  background: linear-gradient(135deg, var(--secondary-dark) 0%, var(--tertiary-dark) 100%);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 32px;
  margin-bottom: 24px;
  text-align: center;
  box-shadow: var(--shadow-md);
}

.welcome-content h2 {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.welcome-content p {
  font-size: 16px;
  color: var(--text-secondary);
  margin-bottom: 24px;
}

.quick-actions {
  display: flex;
  gap: 16px;
  justify-content: center;
  flex-wrap: wrap;
}

.action-card {
  background: var(--tertiary-dark);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 20px;
  cursor: pointer;
  transition: all var(--transition-normal);
  display: flex;
  align-items: center;
  gap: 16px;
  text-align: left;
  color: var(--text-primary);
  text-decoration: none;
  min-width: 250px;
}

.action-card:hover {
  background: var(--hover-bg);
  border-color: var(--accent-gold);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.action-card i {
  font-size: 24px;
  color: var(--accent-gold);
  flex-shrink: 0;
}

.action-card h3 {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 4px;
  color: var(--text-primary);
}

.action-card p {
  font-size: 12px;
  color: var(--text-secondary);
  margin: 0;
}

/* Dashboard Grid */
.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 24px;
}

.dashboard-card {
  background: var(--secondary-dark);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 20px;
  box-shadow: var(--shadow-sm);
  transition: all var(--transition-fast);
}

.dashboard-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.dashboard-card.wide {
  grid-column: 1 / -1;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--border-color);
}

.card-header h3 {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.card-header i {
  color: var(--accent-gold);
  font-size: 18px;
}

.card-action {
  background: none;
  border: none;
  color: var(--accent-gold);
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all var(--transition-fast);
}

.card-action:hover {
  color: var(--text-primary);
}

.card-content {
  flex: 1;
}

/* Button Styles */
.btn {
  border: none;
  border-radius: var(--border-radius);
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  text-decoration: none;
  font-family: inherit;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.btn.primary {
  background: linear-gradient(135deg, var(--accent-gold) 0%, #f0b90b 100%);
  color: var(--primary-dark);
  box-shadow: var(--shadow-sm);
}

.btn.primary:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(252, 213, 53, 0.3);
}

.btn.secondary {
  background: var(--tertiary-dark);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.btn.secondary:hover:not(:disabled) {
  background: var(--hover-bg);
  border-color: var(--accent-gold);
}

.btn.danger {
  background: var(--accent-red);
  color: white;
}

.btn.danger:hover:not(:disabled) {
  background: #e8465d;
  transform: translateY(-1px);
}

/* Form Elements */
.form-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-bottom: 16px;
}

.form-label {
  font-size: 12px;
  font-weight: 500;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-input, .form-select {
  background: var(--tertiary-dark);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 10px 12px;
  color: var(--text-primary);
  font-size: 14px;
  transition: all var(--transition-fast);
  font-family: inherit;
}

.form-input:focus, .form-select:focus {
  outline: none;
  border-color: var(--accent-gold);
  box-shadow: 0 0 0 2px rgba(252, 213, 53, 0.1);
}

/* Status States */
.status-good {
  color: var(--accent-green);
  font-weight: 500;
}

.status-warning {
  color: #faad14;
  font-weight: 500;
}

.status-error {
  color: var(--accent-red);
  font-weight: 500;
}

/* Loading Spinner */
.loading-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loading-spinner.large {
  width: 32px;
  height: 32px;
  border-width: 3px;
}

/* Toast Notifications */
.toast-container {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 2001;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.toast {
  background: var(--secondary-dark);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 16px 20px;
  box-shadow: var(--shadow-lg);
  animation: slideInRight 0.3s ease;
  max-width: 400px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.toast.success {
  border-color: var(--accent-green);
  background: linear-gradient(90deg, var(--secondary-dark) 0%, rgba(2, 192, 118, 0.1) 100%);
}

.toast.error {
  border-color: var(--accent-red);
  background: linear-gradient(90deg, var(--secondary-dark) 0%, rgba(246, 70, 93, 0.1) 100%);
}

.toast.warning {
  border-color: #faad14;
  background: linear-gradient(90deg, var(--secondary-dark) 0%, rgba(250, 173, 20, 0.1) 100%);
}

.toast-icon {
  font-size: 18px;
  flex-shrink: 0;
}

.toast.success .toast-icon {
  color: var(--accent-green);
}

.toast.error .toast-icon {
  color: var(--accent-red);
}

.toast.warning .toast-icon {
  color: #faad14;
}

.toast-content {
  flex: 1;
}

.toast-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 2px;
}

.toast-message {
  font-size: 12px;
  color: var(--text-secondary);
}

.toast-close {
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  font-size: 14px;
  padding: 2px;
  border-radius: 2px;
  transition: all var(--transition-fast);
}

.toast-close:hover {
  background: var(--hover-bg);
  color: var(--text-primary);
}

@keyframes slideInRight {
  from {
      opacity: 0;
      transform: translateX(100%);
  }
  to {
      opacity: 1;
      transform: translateX(0);
  }
}

/* Control Panel */
.control-panel {
  background: var(--secondary-dark);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 24px;
  box-shadow: var(--shadow-sm);
}

.panel-title {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.panel-title i {
  color: var(--accent-gold);
}

.controls-grid {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 120px;
  gap: 20px;
  align-items: end;
}

/* Data Tables */
.data-table {
  background: var(--secondary-dark);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  margin-bottom: 24px;
  overflow: hidden;
  box-shadow: var(--shadow-sm);
}

.table-header {
  background: var(--tertiary-dark);
  padding: 16px 20px;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table-title {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 8px;
}

.table-title i {
  font-size: 18px;
}

.table-container {
  max-height: 500px;
  overflow-y: auto;
}

.empty-state {
  padding: 40px;
  text-align: center;
  color: var(--text-secondary);
  font-size: 14px;
}

/* Modal */
.modal {
  position: fixed;
  z-index: 2000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: var(--secondary-dark);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  width: 90%;
  max-width: 500px;
  box-shadow: var(--shadow-lg);
  animation: modalSlideIn 0.3s ease;
}

@keyframes modalSlideIn {
  from {
      opacity: 0;
      transform: translateY(-20px);
  }
  to {
      opacity: 1;
      transform: translateY(0);
  }
}

.modal-header {
  padding: 24px 24px 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.modal-header h2 {
  color: var(--text-primary);
  font-size: 18px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 0;
}

.modal-close {
  background: none;
  border: none;
  color: var(--text-secondary);
  font-size: 18px;
  cursor: pointer;
  padding: 4px;
  border-radius: var(--border-radius);
  transition: all var(--transition-fast);
}

.modal-close:hover {
  background: var(--hover-bg);
  color: var(--text-primary);
}

.modal-body {
  padding: 24px;
}

.modal-footer {
  padding: 0 24px 24px;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  flex-wrap: wrap;
}

/* Summary Stats */
.summary-stats {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 16px;
}

.summary-item {
  text-align: center;
}

.summary-value {
  font-size: 24px;
  font-weight: 700;
  color: var(--accent-gold);
  margin-bottom: 4px;
}

.summary-label {
  font-size: 11px;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Status Grid */
.status-grid {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.status-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
}

/* Activity List */
.activity-list {
  max-height: 200px;
  overflow-y: auto;
}

.activity-empty {
  text-align: center;
  color: var(--text-muted);
  padding: 40px 20px;
  font-size: 14px;
}

.activity-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 0;
  border-bottom: 1px solid var(--border-color);
}

.activity-item:last-child {
  border-bottom: none;
}

.activity-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  flex-shrink: 0;
}

.activity-icon.success {
  background: rgba(2, 192, 118, 0.2);
  color: var(--accent-green);
}

.activity-icon.warning {
  background: rgba(250, 173, 20, 0.2);
  color: #faad14;
}

.activity-icon.error {
  background: rgba(246, 70, 93, 0.2);
  color: var(--accent-red);
}

.activity-content {
  flex: 1;
}

.activity-title {
  font-size: 13px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 2px;
}

.activity-time {
  font-size: 11px;
  color: var(--text-secondary);
}

/* Responsive Design */
@media (max-width: 1200px) {
  .controls-grid {
      grid-template-columns: 1fr;
      gap: 16px;
  }
  
  .summary-stats {
      grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  :root {
      --sidebar-width: 280px;
  }
  
  .sidebar {
      transform: translateX(-100%);
  }
  
  .sidebar.show {
      transform: translateX(0);
  }
  
  .main-content {
      margin-left: 0;
  }
  
  .page-content {
      padding: 16px;
  }
  
  .header {
      padding: 0 16px;
      height: 56px;
  }
  
  .header-stats {
      display: none;
  }
  
  .page-title {
      font-size: 18px;
  }
  
  .breadcrumb {
      display: none;
  }
  
  .quick-actions {
      flex-direction: column;
