/* Scrollbar Styles */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: var(--primary-dark);
}

::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--text-secondary);
}

/* Focus Styles for Accessibility */
button:focus, input:focus, select:focus {
  outline: 2px solid var(--accent-gold);
  outline-offset: 2px;
}
.mobile-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  backdrop-filter: blur(2px);
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Ensure sidebar shows above overlay on mobile */
@media (max-width: 768px) {
  .sidebar {
    z-index: 1001 !important;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
  }
  
  .sidebar.show {
    transform: translateX(0) !important;
  }
  
  .main-content {
    margin-left: 0 !important;
  }
}
/* Add this to your existing App.css for Price Analysis mobile responsiveness */

/* Chart Panel Mobile Styles */
.chart-panel {
  background: var(--secondary-dark);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  box-shadow: var(--shadow-sm);
  overflow: hidden;
}

.chart-header {
  background: var(--tertiary-dark);
  padding: 16px 20px;
  border-bottom: 1px solid var(--border-color);
}

.chart-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

/* Symbol Input Container */
.symbol-input-container {
  position: relative;
}

/* Analysis Results Container */
.analysis-results {
  margin-top: 24px;
}

/* Mobile Responsive Adjustments */
@media (max-width: 768px) {
  /* Control Panel Mobile */
  .control-panel {
    padding: 16px;
  }
  
  .panel-title {
    font-size: 14px;
    margin-bottom: 16px;
  }
  
  /* Form Groups on Mobile */
  .form-group {
    margin-bottom: 16px;
  }
  
  .form-label {
    font-size: 11px;
    margin-bottom: 6px;
  }
  
  .form-input, .form-select {
    font-size: 16px; /* Prevents zoom on iOS */
    padding: 12px;
  }
  
  /* Chart Panel Mobile */
  .chart-panel {
    margin-bottom: 20px;
  }
  
  .chart-header {
    padding: 12px 16px;
  }
  
  .chart-title {
    font-size: 13px;
  }
  
  /* Symbol Dropdown Mobile */
  .symbol-input-container div[style*="position: absolute"] {
    max-height: 150px !important;
  }
  
  /* Statistics Cards Mobile */
  .analysis-results > div:nth-child(2) {
    grid-template-columns: 1fr 1fr !important;
    gap: 12px !important;
  }
  
  /* Heatmap Mobile Adjustments */
  .analysis-results > div:nth-child(3) {
    grid-template-columns: 1fr !important;
    gap: 16px !important;
  }
  
  /* Trading Insights Mobile */
  .dashboard-card .card-content > div {
    grid-template-columns: 1fr !important;
    gap: 16px !important;
  }
  
  /* Button Mobile */
  .btn.primary {
    padding: 12px 16px;
    font-size: 14px;
  }
  
  /* TradingView Widget Mobile */
  .tradingview-widget-container {
    height: 300px !important;
  }
}

@media (max-width: 480px) {
  /* Extra small mobile adjustments */
  .page-content {
    padding: 12px;
  }
  
  .control-panel {
    padding: 12px;
  }
  
  .panel-title {
    font-size: 13px;
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }
  
  /* Statistics cards 1 column on very small screens */
  .analysis-results > div:nth-child(2) {
    grid-template-columns: 1fr !important;
  }
  
  /* Smaller chart on very small screens */
  .chart-panel div[style*="height: 300px"] {
    height: 250px !important;
  }
  
  /* Reduce padding on insight cards */
  .dashboard-card .card-content > div > div {
    padding: 12px !important;
  }
}

/* Landscape mobile adjustments */
@media (max-width: 768px) and (orientation: landscape) {
  .chart-panel div[style*="height"] {
    height: 250px !important;
  }
  
  .analysis-results > div:nth-child(2) {
    grid-template-columns: repeat(4, 1fr) !important;
  }
  
  .analysis-results > div:nth-child(3) {
    grid-template-columns: 1fr 1fr !important;
  }
}